import 
<template>
  <Navi/>
  <div class="home">
    <input type="button" value="Logout" v-on:click="logout()">
  </div>
</template>

<script>
import Navi from '@/components/Navi'
import router from '@/router/index.js'

export default {
  name: 'Home',
  components: {
    Navi
  },
  methods:{
    logout() {
      this.$store.commit('logout')
      router.push('/login')
    }
  }
}
</script>
